.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;

      li {
        opacity: 1;
      }
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-dark;
  border-radius: 50%;
  transition: color 0.3s ease;

  body.light-purple & {
    color: $c-purple;
  }

  body.light-blue & {
    color: $dark-blue;
  }

  body.dark-blue & {
    color: $light-blue;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $c-light;
    z-index: -1;
    transform: translate(-50%, -50%) scale(0.65);
    transition: transform 0.3s, background-color 0.5s;
    transition-timing-function: cubic-bezier(0.4, 1.1, 0.85, 0.9);

    .loaded & {
      transform: translate(-50%, -50%) scale(1);
    }
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
    transition: color 0.1s ease;
  }

  &:hover,
  .social-block--active & {
    background-color: $c-purple;
    border-color: $c-purple;
    color: $c-light;

    body.light-purple & {
      color: $c-light;
      background-color: $c-dark;
      border-color: $c-dark;
    }

    body.light-blue & {
      color: $c-light;
      background-color: $light-blue;
      border-color: $light-blue;
    }

    body.dark-blue & {
      color: $c-light;
      background-color: $dark-blue;
      border-color: $dark-blue;
    }

    &::before {
      transform: translate(-50%, -50%) scale(1.1);
      border-color: $c-purple;
      background-color: $c-purple;

      body.light-purple & {
        background-color: $c-dark;
        border-color: $c-dark;
      }

      body.light-blue & {
        background-color: $light-blue;
        border-color: $light-blue;
      }

      body.dark-blue & {
        background-color: $dark-blue;
        border-color: $dark-blue;
      }
    }
  }

  &:focus {
    color: $c-purple;
    outline: none;

    body.light-purple & {
      color: $c-dark;
    }

    body.light-blue & {
      color: $light-blue;
    }

    body.dark-blue & {
      color: $dark-blue;
    }

    &::before {
      border-color: $c-purple;

      body.light-purple & {
        border-color: $c-dark;
      }

      body.light-blue & {
        border-color: $light-blue;
      }

      body.dark-blue & {
        border-color: $dark-blue;
      }
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;

      body.light-purple & {
        background-color: $c-dark;
        border-color: $c-dark;
      }

      body.light-blue & {
        background-color: $light-blue;
        border-color: $light-blue;
      }

      body.dark-blue & {
        background-color: $dark-blue;
        border-color: $dark-blue;
      }
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;

    li {
      transform: translateY(0);
      opacity: 1;
      &:nth-child(2) {
        transition-delay: 100ms;
      }
      &:nth-child(3) {
        transition-delay: 200ms;
      }
    }
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;
    opacity: 0;
    transform: translateY(0.5rem);
    transition: all 0.3s ease;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: all 0.3s ease;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
  }

  &:hover {
    color: $c-purple;

    body.light-purple & {
      color: $c-dark;
    }

    body.light-blue & {
      color: $light-blue;
    }

    body.dark-blue & {
      color: $dark-blue;
    }
  }

  &:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-purple;
    outline: none;
  }

  &:active,
  &:active:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-light;
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}
